
import { IonContent, IonPage, isPlatform } from "@ionic/vue";
import { defineComponent, onMounted, reactive, ref, watchEffect } from "vue";
import { create as createConfetti } from "canvas-confetti";
import { useRouter } from "vue-router";
import { App } from "@capacitor/app";
import { KeepAwake } from "@capacitor-community/keep-awake";
import { NativeAudio } from "@capacitor-community/native-audio";
import { useTimer } from "vue-timer-hook";
import { useMainStore } from "../store/main";
import { Team, useGameStore } from "../store/game";
import Modal from "./Modal.vue";
import { CheckIcon, ExclamationIcon } from '@heroicons/vue/outline'

const audios: any = {};

if (isPlatform("capacitor")) {
  audios["tada"] = {
    play: () => {
      NativeAudio.play({
        assetId: "tada",
        time: 0,
      });
    },
  };
  audios["horn"] = {
    play: () => {
      NativeAudio.play({
        assetId: "horn",
        time: 0,
      });
    },
  };
  KeepAwake.keepAwake();
} else {
  audios["tada"] = new Audio("assets/tada.mp3");
  audios["horn"] = new Audio("assets/horn.mp3");
}

const makeSound = (name: string) => {
  if (isPlatform("capacitor")) {
    audios[name].play();
  } else {
    audios[name].play();
  }
};

export default defineComponent({
  name: "Game",
  components: {
    CheckIcon,
    ExclamationIcon,
    Modal,
    IonContent,
    IonPage,
  },
  ionViewDidEnter() {
    this.initGameLoop();
  },
  setup() {
    const appStateChange = ref();
    const winners = ref([] as Team[]);
    const modals = reactive({
      changePlayer: false,
      winner: false,
      pause: false,
    });
    const router = useRouter();
    const game = useGameStore();
    const main = useMainStore();
    const timer = useTimer(1, false);
    const canvas = ref<HTMLCanvasElement>();
    let confetti: confetti.CreateTypes;

    const createTime = () => {
      const expiryTimestamp = new Date();
      expiryTimestamp.setSeconds(expiryTimestamp.getSeconds() + 45);
      return expiryTimestamp.getTime();
    };

    const nextRound = () => {
      main.nextGuess();
      timer.restart(createTime());
    };

    const skipGuess = () => {
      main.nextGuess(true);
    };

    const createLadder = (): Team[] => {
      const sorted = game.teams.sort((a: Team, b: Team) => {
        return a.score > b.score ? -1 : 1;
      });
      return sorted;
    };

    const playConfetti = () => {
      return confetti({
        angle: 90,
        spread: 60,
        particleCount: 350,
        ticks: 400,
      });
    };

    const addScore = async () => {
      if (game.team.score < 10) {
        game.team.score += 1;
        game.addScore();
        if (game.team.score >= 10) {
          timer.pause();
          winners.value = createLadder();
          modals.winner = true;
          // window.location.hash = '#winner';
          makeSound("tada");
          await game.save();
          await playConfetti();
        }
      }
    };

    const validGuess = () => {
      main.nextGuess(false, true);
      addScore();
    };

    const setupCanvas = () => {
      const options = {
        useWorker: true,
        resize: true,
      };
      if (isPlatform("android") && isPlatform("capacitor")) {
        options.resize = false;
      }
      if (canvas.value) {
        confetti = createConfetti(canvas.value, options);
      }
    };

    const initGameLoop = () => {
      game.resetScore();
      winners.value = [];
      main.nextGuess();
      game.nextTeam();
      modals.changePlayer = true;
    }
    
    onMounted(() => {
      watchEffect(async () => {
        if(timer.isExpired.value) {
            makeSound("horn");
            await game.nextTeam();
            modals.changePlayer = true;
        }
      })
        appStateChange.value = App.addListener("appStateChange", (state) => {
          if (!state.isActive) {
            timer.pause();
          } else {
            timer.resume();
          }
        });
      setupCanvas();
    });

    return {
      winners,
      initGameLoop,
      validGuess,
      skipGuess,
      nextRound,
      modals,
      router,
      canvas,
      main,
      timer,
      game,
    };
  },
});
